import { useState } from "react";

import {
  MainContainer,
  DescriptionContainer,
  StyledTitle,
  StyledText,
  BallContainer,
  BallBlack,
  BallWhite,
  BallTextContainer,
  EightContainer,
  ButtonsContainer,
  StyledButton,
} from "./MagicBall.styled";

const answers = [
  ",  ",
  " ",
  " ",
  " ",
  "   ",
  "  , ",
  "",
  " ",
  "",
  " ,  ",
  "   ",
  "  - ",
  "  ,  ",
  "   ",
  " ",
  " ,   ",
  " ",
  "    ",
  "  ",
  "    ",
  "  ",
];

export const MagicBall = () => {
  const [answer, setAnswer] = useState("");

  const getAnswer = (e) => {
    e.preventDefault();

    const answerIndex = Math.floor(Math.random() * answers.length);

    setAnswer(answers[answerIndex]);
  };

  const resetAnswer = (e) => {
    e.preventDefault();
    setAnswer("");
  };

  return (
    <>
      <MainContainer>
        <DescriptionContainer>
          <StyledTitle>
            !       .
          </StyledTitle>
          <StyledText>
            ,       
            "".
          </StyledText>
          <p>
            *!     ,   
              ,    
             .
          </p>
        </DescriptionContainer>
        <BallContainer>
          <BallBlack>
            <BallWhite>
              <BallTextContainer>
                {answer ? answer : <EightContainer>8</EightContainer>}
              </BallTextContainer>
            </BallWhite>
          </BallBlack>
        </BallContainer>
        <ButtonsContainer>
          <StyledButton onClick={getAnswer}></StyledButton>
          <StyledButton onClick={resetAnswer}></StyledButton>
        </ButtonsContainer>
      </MainContainer>
    </>
  );
};

